import React from 'react';
import styled, { css } from 'styled-components';

const Style = css`
  display: flex;

  h2 {
    display: none;
  }

  ul {
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
  }
`;

const NavList = styled(({ children, labelledby, titleLabelledby, ...rest }) => (
  <nav aria-labelledby={labelledby} {...rest}>
    <h2 id={labelledby}>{titleLabelledby}</h2>

    <ul>
      {children}
    </ul>
  </nav>
))`
  ${Style}
`;


export default NavList;
