import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

import * as VARIATIONS from './constant';

import { convertString } from '../helpers';

const Style = css`
  letter-spacing: -0.01em;
  color: ${({ textColor }) => `var(${textColor})`};

  ${({ token, isItalic }) =>
    VARIATIONS.tokens[convertString.constant(token)]
      ? css`
          font: ${isItalic ? 'italic' : 'normal'} var(${`--${token}`});
        `
      : css`
        font: var(--text-weight-regular) var(--text-size-sm) var(--text-font-family);

        &:after {
          content: ' [ERRO] - ${`${token} - esse token não exite`}';
          font-weight: var(--text-weight-bold);
          color: var(--color-red-50);
        }
    `}

  ${({ token }) =>
    token.includes('xxl') &&
    css`
      letter-spacing: -0.02em;
    `}

  ${({ token }) =>
    token.includes('xs') &&
    css`
      letter-spacing: 0;
    `}
`;

const TextComponent = styled(({ as, size, weight, isItalic, textColor, children, token, ...otherProps }) => (
  <p isItalic={isItalic} textColor={textColor} token={token} {...otherProps}>
    {children}
  </p>
))`
  ${Style}
`;

const Text = props => <TextComponent {...props} />;

Text.tokens = VARIATIONS.tokens;

Text.displayName = 'Text';

Text.defaultProps = {
  isItalic: false,
  as: 'p',
  textColor: '--color-gray-100',
  token: Text.tokens.FONT_SIZE_SM_REGULAR
};

Text.propTypes = {
  /**
   * set font style
   */
  isItalic: PropTypes.bool,

  /**
   * set the tag should be rendered
   */
  as: PropTypes.oneOfType([PropTypes.node, PropTypes.string]),

  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array]).isRequired,

  /**
   * set color variable
   */
  textColor: PropTypes.string,

  /**
   * set font size
   */
  token: PropTypes.oneOf([
    Text.tokens.FONT_SIZE_XXL_BOLD,
    Text.tokens.FONT_SIZE_XXL_SEMIBOLD,
    Text.tokens.FONT_SIZE_XL_SEMIBOLD,
    Text.tokens.FONT_SIZE_LG_SEMIBOLD,
    Text.tokens.FONT_SIZE_MD_BOLD,
    Text.tokens.FONT_SIZE_MD_SEMIBOLD,
    Text.tokens.FONT_SIZE_MD_REGULAR,
    Text.tokens.FONT_SIZE_SM_BOLD,
    Text.tokens.FONT_SIZE_SM_SEMIBOLD,
    Text.tokens.FONT_SIZE_SM_REGULAR,
    Text.tokens.FONT_SIZE_XS_BOLD,
    Text.tokens.FONT_SIZE_XS_SEMIBOLD,
    Text.tokens.FONT_SIZE_XS_REGULAR
  ])
};

export default Text;
