export function getContrastHex(hexcolor) {
  const color = hexcolor.replace('#', '');
  return parseInt(color, 16) > 0xffffff / 2 ? '--color-gray-100' : '--color-white';
}

export const convertString = {
  constant(str) {
    return str
      .toUpperCase()
      .replace(/-/g, '_')
      .replace(/\s/g, '');
  },
  cammel(str) {
    return str
      .replace(/ /g, '')
      .split('-')
      .map((word, index) => {
        if (index) {
          return `${word[0].toUpperCase()}${word.slice(1)}`;
        }

        return word;
      })
      .join('');
  }
};

export const KINDS = {
  DEFAULT: 'default',
  PRIMARY: 'primary',
  SUCCESS: 'success',
  DANGER: 'danger',
  WARNING: 'warning',
  HELP: 'help'
};

export default { getContrastHex, convertString, KINDS };
