import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import GlobalDesignTokens from './GlobalDesignTokens';
import * as VARIATIONS from './constant';

const Theme = styled(({ withoutDesignTokens, children, value, ...otherProps }) => (
  <main data-theme={value} {...otherProps}>
    {!withoutDesignTokens && <GlobalDesignTokens />}
    {children}
  </main>
))`
  @import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i,800,800i&display=swap');
`;

Theme.variations = VARIATIONS;
Theme.displayName = 'Theme';
Theme.defaultProps = {
  withoutDesignTokens: false,
  value: Theme.variations.MARKETING,
  children: null
};

Theme.propTypes = {
  withoutDesignTokens: PropTypes.bool,
  value: PropTypes.string,
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array])
};

export default Theme;
