/* eslint-disable react/no-multi-comp */
/* eslint-disable max-classes-per-file */
// eslint-disable-next-line import/no-unresolved
import React from 'react';

import configuration from 'front-hub-application-host-configuration';
import contextPublicApiFactory from '../context-public-api';
import watchURLChangesFactory from './watch-url-changes';
import RootCompoment from './root-compoment';
import i18nFactory from '../i18n-factory';

export default function rootCompomentBuilder(fronthubInstance, MicrofrontendComponent, Router) {
  const contextPublicApi = contextPublicApiFactory(fronthubInstance);
  const watchURLChanges = watchURLChangesFactory(contextPublicApi);
  const localizedMicrofrontendComponent = configuration.imDoingWrong.disableLocalization
    ? MicrofrontendComponent
    : i18nFactory(MicrofrontendComponent);

  return ({ params }) => (
    <RootCompoment
      MicrofrontendComponent={localizedMicrofrontendComponent}
      Router={Router}
      WatchURLChanges={watchURLChanges}
      params={params}
      contextPublicApi={contextPublicApi}
    />
  );
}
