import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { getContrastHex } from '../helpers';

const Style = css`
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;

  ${({ color }) => css`
    color: var(${getContrastHex(color)});
    background-color: ${color};
  `}
`;

const AvatarFallbackComponent = styled(({ text, color, ...rest }) => {
  const words = text.split(' ');
  let children = words[0].substring(0, 2);

  if (words.length > 1) {
    children = words[0].charAt();
    children += words[words.length - 1].charAt();
  }

  return <span {...rest}>{children}</span>;
})`
  ${Style}
`;

const AvatarFallback = props => <AvatarFallbackComponent {...props} />;

AvatarFallback.displayName = 'AvatarFallback';

AvatarFallback.propTypes = {
  /**
   * set text
   */
  text: PropTypes.string.isRequired,

  /**
   * set color
   */
  color: PropTypes.string.isRequired
};

export default AvatarFallback;
