import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';

const Style = css`
  border-radius: var(--border-radius-xs);
  cursor: pointer;
  display: flex;
  font: var(--font-size-md-regular);
  padding: var(--size-spacing-02) var(--size-spacing-04);
  transition: background-color var(--motion-duration-fast) var(--motion-timing-function-ease-in-out);
  border: none;

  &:disabled {
    cursor: default;
    opacity: 0.4;
  }

  /*
  * @TODO
  * Colocar todas as cores possíveis
  */
  ${({ kind }) =>
    kind === 'primary' &&
    css`
      background-color: var(--color-primary-60);
      color: var(--color-white);

      &:hover {
        background-color: var(--color-primary-50);
      }

      &:active {
        background-color: var(--color-primary-70);
      }

      &:focus {
        outline-color: var(--color-primary-60);
      }
    `}
`;

const ButtonBaseComponent = styled(({ kind, children, type, ...rest }) => (
  // eslint-disable-next-line react/button-has-type
  <button type={type} {...rest}>
    {children}
  </button>
))`
  ${Style}
`;

const ButtonBase = props => <ButtonBaseComponent {...props} />;

ButtonBase.defaultProps = {
  children: '',
  type: 'button'
};

ButtonBase.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]),
  type: PropTypes.oneOf(['button', 'submit', 'reset']),
  kind: PropTypes.oneOf(['primary']).isRequired
};

export default ButtonBase;
