import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import ButtonBase from '../ButtonBase';

const Style = css`
  padding-left: var(--size-spacing-03);
  padding-right: var(--size-spacing-03);
`;

const IconButtonComponent = styled(({ kind, children, ...rest }) => (
  <ButtonBase kind={kind} {...rest}>
    {children}
  </ButtonBase>
))`
  ${Style}
`;

const IconButton = props => <IconButtonComponent {...props} />;

IconButton.types = ButtonBase.types;

IconButton.defaultProps = {
  children: '',
  type: 'button',
  kind: 'primary'
};

IconButton.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]),
  type: PropTypes.oneOf(['button', 'submit', 'reset']),
  kind: PropTypes.oneOf(['primary'])
};

export default IconButton;
