/* eslint-disable import/no-unresolved */
import i18next from 'i18next';
import configuration from 'front-hub-application-host-configuration';
import resources from 'front-hub-locales';

const { intl } = configuration;

export default (...plugins) => {
  const name = MICROFRONTEND_NAME;
  const i18n = i18next.createInstance();
  plugins.forEach(i18n.use.bind(i18n));
  i18n.init({
    lng: intl.language,
    fallbackLng: intl.fallbackLng,
    debug: true,
    resources: Object.keys(resources).reduce(
      (acc, lgn) => ({
        ...acc,
        [lgn]: {
          [name]: resources[lgn]
        }
      }),
      {}
    ),
    ns: [name],
    defaultNS: name
  });
  return i18n;
};
