/* eslint-disable import/prefer-default-export */
import { applicationCache } from '../../core';
import microFrontendProduction from './microfrontend-production';

const application = applicationCache();

export const Connect = Component => {
  const teste = `____app_${Math.floor(Math.random() * 100)}`;
  window[teste] = Component;
  if (typeof application.connect !== 'function')
    throw new Error('front-hub: private connect function was not defined before public Connect call');
  return application.connect(configuration => {
    // we should remove this call to avoid coupling between front-hub and microfrontends builds
    return microFrontendProduction(Component, configuration);
  });
};
