import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

import config, { DIMENSIONS } from './config';

const Container = styled.div`
  margin-right: auto;
  margin-left: auto;
  max-width: 100%;
  box-sizing: border-box;

  ${props => css`
    ${DIMENSIONS.map(
      dimension =>
        config(props).container[dimension] &&
        config(props).media[dimension]`
        padding-left: ${config(props).paddingWidth[dimension]};
        padding-right: ${config(props).paddingWidth[dimension]};
      `
    )}
  `}

  ${props =>
    !props.fluid &&
    css`
      ${DIMENSIONS.map(
        dimension =>
          config(props).container[dimension] &&
          config(props).media[dimension]`
          ${config(props).container[dimension] !== 'full' ? `width: ${config(props).container[dimension]};` : 'width: 100%;'}
        `
      )}
    `}

  ${({ debug }) =>
    debug &&
    css`
      background-color: var(--color-primary-50);
    `}
`;

Container.propTypes = {
  fluid: PropTypes.bool,
  children: PropTypes.node,
  debug: PropTypes.bool
};

export default Container;
