import fronthubPublicApiFactory from '../comunication-api';

export const methods = ['dispatchEvent', 'delegate', 'registerDelegate', 'addEventListener'];

export default function contextPublicApiFactory(fronthubInstance) {
  const publicApi = fronthubPublicApiFactory(fronthubInstance);

  return methods.reduce(
    (acc, cur) => ({
      ...acc,
      [cur]: publicApi[cur]
    }),
    {}
  );
}
