/* eslint-disable react/react-in-jsx-scope */
import React from 'react';
import ReactDOM from 'react-dom';
import { MemoryRouter } from 'react-router-dom';

import rootCompomentFactory from './microfrontend-root/root-component-factory';
import instancePublicApi from './instance-public-api';

export default function microfrontendFactory(MicrofrontendComponent, configuration) {
  const instance = {
    listeners: {},
    delegates: {},
    deferedDelegates: {},
    configuration
  };
  // rootCompomentBuilder(fronthubInstance, MicrofrontendComponent, BrowserRouter)
  const RootComponent = rootCompomentFactory(instance, MicrofrontendComponent, MemoryRouter);
  const publicApi = instancePublicApi(instance);
  return {
    ...publicApi,
    renderAt(DOMElement, params = {}) {
      ReactDOM.render(<RootComponent params={params} />, DOMElement);
    }
  };
}
