import React from 'react';
import { withRouter } from 'react-router-dom';

import URLManager from '../../url-manager';

export default contextPublicApi =>
  withRouter(
    class extends React.Component {
      constructor(props) {
        super();
        const urlManager = URLManager(contextPublicApi);
        props.history.listen(urlManager.listen);
      }

      render() {
        return null;
      }
    }
  );
