import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import config, { DIMENSIONS } from './config';

const Column = styled.div`
  box-sizing: border-box;
  flex: 1 0 auto;
  max-width: 100%;

  ${props =>
    !props.noGutter &&
    css`
      ${DIMENSIONS.map(
        dimension =>
          config(props).breakpoints[dimension] &&
          config(props).media[dimension]`
          padding-right: ${parseFloat(config(props).gutterWidth[dimension]) / 2}rem;
          padding-left: ${parseFloat(config(props).gutterWidth[dimension]) / 2}rem;
        `
      )}
    `}

  ${props => css`
    ${DIMENSIONS.map(
      dimension =>
        config(props).breakpoints[dimension] &&
        config(props).media[dimension]`
      ${props[dimension] &&
        `
        flex: 1 1 ${(props[dimension] / config(props).columns[dimension]) * 100}%;
        max-width: ${(props[dimension] / config(props).columns[dimension]) * 100}%;
        display: ${props.hidden && props.hidden.includes(dimension) ? 'none' : 'flex'}
      `}
    `
    )}
  `}

  ${props =>
    props.offset &&
    css`
      ${DIMENSIONS.map(
        dimension =>
          config(props).breakpoints[dimension] &&
          config(props).media[dimension]`
      ${
        typeof props.offset === 'object'
          ? props.offset[dimension] !== undefined &&
            `margin-left: ${props.offset[dimension] > 0 ? (props.offset[dimension] / config(props).columns[dimension]) * 100 : 0}%`
          : `margin-left: ${(props.offset / config(props).columns.xs) * 100}%`
      };
    `
      )}
    `}

  ${props =>
    props.reverse &&
    css`
      ${Array.isArray(props.reverse)
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`
        flex-direction: ${props.reverse.indexOf(dimension) !== -1 ? 'column-reverse' : 'column'};
      `
          )
        : 'flex-direction: column-reverse;'}
    `}

  ${props =>
    props.align &&
    css`
      ${typeof props.align === 'object'
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`${props.align[dimension] && `align-items: ${props.align[dimension]}`}`
          )
        : `align-items: ${props.align};`}
    `}

  ${props =>
    props.justify &&
    css`
      ${typeof props.justify === 'object'
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`${props.justify[dimension] && `justify-content: ${props.justify[dimension]}`}`
          )
        : `justify-content: ${props.justify};`}
    `}

  ${({ debug }) =>
    debug &&
    css`
      background-color: var(--color-primary-20);

      &:after {
        content: '';
        background-color: var(--color-primary-40);
        width: 100%;
        height: 20px;
        display: block;
      }
    `}
`;

const numberOrString = PropTypes.oneOfType([PropTypes.string, PropTypes.number]);

const stringOrObject = PropTypes.oneOfType([PropTypes.string, PropTypes.object]);

const numberOrObject = PropTypes.oneOfType([PropTypes.bool, PropTypes.object]);

const boolOrArray = PropTypes.oneOfType([PropTypes.bool, PropTypes.array]);

Column.propTypes = {
  mobileSmall: numberOrString,
  mobile: numberOrString,
  tablet: numberOrString,
  desktop: numberOrString,
  desktopLarge: numberOrString,
  align: stringOrObject,
  justify: stringOrObject,
  offSet: numberOrObject,
  reverse: boolOrArray,
  noGutter: PropTypes.bool,
  children: PropTypes.node,
  debug: PropTypes.bool,
  hidden: PropTypes.arrayOf(PropTypes.string)
};

export default Column;
