/* globals __webpack_require__ */
/** @format */
import { NAMESPACE } from './constants';

const scope = typeof window !== 'undefined' ? window : global;
const instance = scope[NAMESPACE] || (scope[NAMESPACE] = {});

const cache = {};

function getCache(cacheName) {
  if (Object.prototype.hasOwnProperty.call(instance, cacheName)) {
    cache[cacheName] = instance[cacheName];
  } else if (Object.prototype.hasOwnProperty.call(cache, cacheName)) {
    cache[cacheName] = instance[cacheName];
  } else {
    cache[cacheName] = {};
    instance[cacheName] = cache[cacheName];
  }
  return cache[cacheName];
}
export function applicationCache() {
  return instance;
}

export function manifestGetCache() {
  return getCache('manifests');
}

export function libsGetCache() {
  return getCache('libs');
}

export function getKey(manifestInstance, commons, name) {
  const isMicrofrontend = manifestInstance.has(name);
  if (isMicrofrontend) {
    return manifestInstance.get(name).key;
  }
  return /\w@\d/.test(name) ? name : `${name}@${commons}`;
}

export function getScriptSrc(manifestInstance, name) {
  const isCommonsDependency = manifestInstance.isCommonsDependency(name);
  if (isCommonsDependency) {
    return `${manifestInstance.repository}/static/commons/${name}.js`;
  }
  return manifestInstance.path;
}

export const isServer = !(typeof window !== 'undefined' && window.document && window.document.createElement);

// eslint-disable-next-line camelcase
export const isWebpack = () => typeof __webpack_require__ !== 'undefined';

export function parseToSafeJson(json) {
  return JSON.parse(JSON.stringify(json || {}));
}
