import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

import { sizes, colorsPallet } from './constant';

import Fallback from './AvatarFallback';

const Style = css`
  width: 40px;
  height: 40px;
  border-radius: 100%;
  font: var(--font-size-md-semibold);
  overflow: hidden;

  ${({ size }) =>
    size === sizes.MD &&
    css`
      width: 80px;
      height: 80px;
      font: var(--font-size-xxl-semibold);
    `}

  ${({ size }) =>
    size === sizes.LG &&
    css`
      width: 120px;
      height: 120px;
      font: var(--font-size-xxl-semibold);
    `}

  ${({ isBrand }) =>
    isBrand &&
    css`
      border-radius: var(--border-radius-sm);
    `}

  img {
    width: 100%;
  }
`;

const AvatarComponent = styled(({ isBrand, color, src, alt, ...rest }) =>
  src ? (
    <span {...rest}>
      <img src={src} alt={alt} />
    </span>
  ) : (
    <Fallback {...rest} color={color} text={alt} />
  )
)`
  ${Style}
`;

const Avatar = props => <AvatarComponent {...props} />;

Avatar.sizes = sizes;

Avatar.colors = colorsPallet;

Avatar.displayName = 'Avatar';

Avatar.defaultProps = {
  src: '',
  size: Avatar.sizes.SM,
  isBrand: false,
  color: Avatar.colors.COLOR_GRAY_30
};

Avatar.propTypes = {
  /**
   * set image source
   */
  src: PropTypes.string,

  /**
   * set alt text
   */
  alt: PropTypes.string.isRequired,

  /**
   * set avatar width and height
   */
  size: PropTypes.oneOf([Avatar.sizes.SM, Avatar.sizes.MD, Avatar.sizes.LG]),

  /**
   * set avatar color and background-color
   */
  color: PropTypes.oneOf(Object.values(Avatar.colors)),

  /**
   * set avatar type
   */
  isBrand: PropTypes.bool
};

export default Avatar;
