import React, { useState, useEffect } from 'react';

import { Times } from '@resultadosdigitais/tangram-react-icons';

import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { KINDS } from '../helpers';

const resolveKindVariable = (kind, removable) => {
  const kindMap = {
    DEFAULT: {
      background: 'var(--color-gray-10)',
      border: 'var(--color-gray-30)',
      color: 'var(--color-gray-70)',
      button: 'var(--color-gray-40)'
    },
    PRIMARY: {
      background: 'var(--color-primary-10)',
      border: 'var(--color-primary-30)',
      color: 'var(--color-primary-70)',
      button: 'var(--color-primary-40)'
    },
    SUCCESS: {
      background: 'var(--color-green-10)',
      border: 'var(--color-green-40)',
      color: 'var(--color-green-70)',
      button: 'var(--color-green-50)'
    },
    DANGER: {
      background: 'var(--color-red-10)',
      border: 'var(--color-red-30)',
      color: 'var(--color-red-70)',
      button: 'var(--color-red-40)'
    },
    WARNING: {
      background: 'var(--color-yellow-20)',
      border: 'var(--color-yellow-60)',
      color: 'var(--color-yellow-90)',
      button: 'var(--color-yellow-70)'
    },
    HELP: {
      background: 'var(--color-purple-10)',
      border: 'var(--color-purple-30)',
      color: 'var(--color-purple-70)',
      button: 'var(--color-purple-40)'
    }
  };
  if (removable) return kindMap.DEFAULT;
  return kindMap[kind.toUpperCase()] ? kindMap[kind.toUpperCase()] : kindMap.DEFAULT;
};

const Span = styled.span`
  border-width: 1px;
  border-style: solid;
  border-radius: var(--border-radius-xs);

  font: var(--font-size-xs-bold);

  padding: 0 var(--size-spacing-02);
  height: 22px;
  max-width: 240px;
  display: flex;
  align-items: center;
  text-transform: ${({ kind, removable }) => (kind === KINDS.DEFAULT && removable ? 'none' : 'uppercase')};

  .tag-content {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  svg {
    fill: var(--color-gray-70);
    height: 1rem;
    width: 1rem;
    padding: 0 var(--size-spacing-01) 0 0;
  }

  button {
    padding: 0;
    margin-left: var(--size-spacing-01);
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  ${({ kind, removable }) => {
    const { border, background, color, button } = resolveKindVariable(kind, removable);
    return css`
      border-color: ${border};
      background-color: ${background};
      color: ${color};
      svg {
        fill: ${color};
      }
      button > svg {
        fill: ${button};
      }
    `;
  }}

  ${({ removable }) =>
    removable &&
    css`
      &:hover {
        border-color: var(--color-gray-40);
        button > svg {
          fill: var(--color-gray-70);
        }
      }
      &:active {
        background-color: var(--color-gray-20);
      }
      &:focus {
        outline-color: var(--color-gray-30);
      }
    `}
`;

const Tag = ({ kind, children, startIcon, removable, onClick, ...rest }) => {
  const [visible, setVisible] = useState(true);

  useEffect(() => {
    if (kind !== 'default' && children.split(' ').length > 2) {
      console.warn(`Componente "Tag de sistema - ${kind}", recomendável não usar mais do que duas palavras.`);
    }

    return () => onClick();
  });

  return (
    visible && (
      <Span kind={kind} removable={removable} {...rest}>
        {startIcon && startIcon}
        <span className="tag-content">{children}</span>

        {removable && (
          <button
            type="button"
            onClick={() => {
              setVisible(false);
            }}
          >
            <Times title="Remove tag" titleId="idIconCloseTag" />
          </button>
        )}
      </Span>
    )
  );
};

Tag.displayName = 'Tag';
Tag.kind = KINDS;

Tag.defaultProps = {
  kind: Tag.kind.DEFAULT,
  startIcon: null,
  removable: false,
  onClick: () => {}
};

Tag.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]).isRequired,
  kind: PropTypes.oneOf([Tag.kind.DEFAULT, Tag.kind.PRIMARY, Tag.kind.SUCCESS, Tag.kind.WARNING, Tag.kind.DANGER, Tag.kind.HELP]),
  startIcon: PropTypes.node,
  removable: PropTypes.bool,
  onClick: PropTypes.func
};

export default Tag;
