import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import ButtonBase from '../ButtonBase';

const Style = css`
  ${({ startIcon }) =>
    startIcon &&
    css`
      padding-left: var(--size-spacing-03);

      svg {
        margin-right: var(--size-spacing-02);
      }
    `}

  ${({ endIcon }) =>
    endIcon &&
    css`
      padding-right: var(--size-spacing-03);

      svg {
        margin-left: var(--size-spacing-02);
      }
    `}
`;

const ButtonComponent = styled(({ kind, children, startIcon, endIcon, ...rest }) => (
  <ButtonBase kind={kind} {...rest}>
    {startIcon && startIcon}
    {children}
    {endIcon && endIcon}
  </ButtonBase>
))`
  ${Style}
`;

const Button = props => <ButtonComponent {...props} />;

Button.defaultProps = {
  children: '',
  type: 'button',
  kind: 'primary',
  startIcon: null,
  endIcon: null
};

Button.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.array, PropTypes.node]),
  type: PropTypes.oneOf(['button', 'submit', 'reset']),
  kind: PropTypes.oneOf(['primary']),
  startIcon: PropTypes.node,
  endIcon: PropTypes.node
};

export default Button;
