import { css } from 'styled-components';
import {
  breakpointSm,
  breakpointMd,
  breakpointLg,
  breakpointXl,
  sizeLayout01,
  sizeLayout02,
  sizeLayout03,
  sizeLayout04
} from '@resultadosdigitais/tangram-design-tokens';

export const DIMENSIONS = ['mobileSmall', 'mobile', 'tablet', 'desktop', 'desktopLarge'];
export const BASE_CONF = {
  columns: {
    mobileSmall: 4,
    mobile: 8,
    tablet: 12,
    desktop: 12,
    desktopLarge: 12
  },
  gutterWidth: {
    mobileSmall: sizeLayout01, // 16px
    mobile: sizeLayout01,
    tablet: sizeLayout02, // 24px
    desktop: sizeLayout02,
    desktopLarge: sizeLayout02
  },
  paddingWidth: {
    mobileSmall: sizeLayout01, // 16px
    mobile: sizeLayout03, // 32px
    tablet: sizeLayout04, // 48px
    desktop: sizeLayout04,
    desktopLarge: sizeLayout04
  },
  container: {
    mobileSmall: 'full', // 'full' = max-width: 100%
    mobile: 'full', // 'full' = max-width: 100%
    tablet: 'full', // 'full' = max-width: 100%
    desktop: breakpointLg, // max-width: 1280px
    desktopLarge: breakpointXl // max-width: 1440px
  },
  breakpoints: {
    mobileSmall: sizeLayout01,
    mobile: breakpointSm, // 768px
    tablet: breakpointMd, // 1024px
    desktop: breakpointLg, // 1280px
    desktopLarge: breakpointXl // 1440px
  },
  media: {}
};

function makeMedia(media) {
  return (...args) => css`
    @media ${media} {
      ${css(...args)}
    }
  `;
}

const resolveConfig = () => {
  const conf = {
    ...BASE_CONF
  };

  conf.media = Object.keys(conf.breakpoints).reduce((media, breakpoint) => {
    const breakpointWidth = conf.breakpoints[breakpoint];
    const mediaBP = media;

    mediaBP[breakpoint] = makeMedia(breakpointWidth && `(min-width: ${breakpointWidth})`);

    return mediaBP;
  }, {});

  return conf;
};

export default function config(props = {}) {
  const conf = resolveConfig(props);

  return conf;
}
