import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import config, { DIMENSIONS } from './config';

const Row = styled.div`
  box-sizing: border-box;
  display: flex;
  flex: 1 0 auto;
  flex-wrap: wrap;

  ${props => css`
    ${DIMENSIONS.map(
      dimension =>
        config(props).container[dimension] &&
        config(props).media[dimension]`
      margin-left: -${parseFloat(config(props).gutterWidth[dimension]) / 2}rem;
      margin-right: -${parseFloat(config(props).gutterWidth[dimension]) / 2}rem;
    `
    )}
  `}

  ${props =>
    props.reverse &&
    css`
      ${Array.isArray(props.reverse)
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`
        flex-direction: ${props.reverse.indexOf(dimension) !== -1 ? 'row-reverse' : 'row'};
      `
          )
        : 'flex-direction: row-reverse;'}
    `}

  ${props =>
    props.align &&
    css`
      ${typeof props.align === 'object'
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`${props.align[dimension] && `align-items: ${props.align[dimension]}`}`
          )
        : `align-items: ${props.align};`}
    `}

  ${props =>
    props.justify &&
    css`
      ${typeof props.justify === 'object'
        ? DIMENSIONS.map(
            dimension =>
              config(props).breakpoints[dimension] &&
              config(props).media[dimension]`${props.justify[dimension] && `justify-content: ${props.justify[dimension]}`}`
          )
        : `justify-content: ${props.justify};`}
    `}

  ${({ debug }) =>
    debug &&
    css`
      background-color: var(--color-red-10);
    `}
`;

const boolOrArray = PropTypes.oneOfType([PropTypes.bool, PropTypes.array]);

const stringOrObject = PropTypes.oneOfType([PropTypes.string, PropTypes.object]);

Row.propTypes = {
  reverse: boolOrArray,
  align: stringOrObject,
  justify: stringOrObject,
  children: PropTypes.node,
  debug: PropTypes.bool
};

export default Row;
