import React from 'react';
import { FronthubProvider } from 'front-hub-context-api';
import { parseToSafeJson } from '../../../core';

export default ({ Router, MicrofrontendComponent, WatchURLChanges, contextPublicApi, params }) => {
  return (
    <Router>
      <WatchURLChanges />
      <FronthubProvider value={contextPublicApi}>
        <MicrofrontendComponent {...parseToSafeJson(params)}></MicrofrontendComponent>
      </FronthubProvider>
    </Router>
  );
};
